
import '../styles/global.css';
import { FormEvent, useState } from 'react';
import axios from 'axios';
import { Envelope, Lock, UserCircle } from 'phosphor-react';

import { Checkbox } from '../components/Checkbox';
import { Button } from '../components/Button';
import { Heading } from '../components/Heading';
import { TextInput } from '../components/TextInput';
import { Text } from '../components/Text';
import Logo from '../Logo';

import imagem from '../images/reactLogo.svg'

export function SignIn() {
    const [isUserSignedIn, setIsUserSignedIn] = useState(false)

    async function handleSignIn(event: FormEvent) {
        //previne o comportamento padrao de um submit em um form, evitando redirecionamento do usuario
        event.preventDefault()

        await axios.post('/sessions', {
            email: 'mayararagao1@gmail.com',
            password: '12345678'
        })

        setIsUserSignedIn(true)
    }


    return (
        <div className='w-screen h-screen bg-gray-900 flex flex-col items-center justify-center text-gray-100'>
            <header className='flex flex-col items-center'>
                <img src={imagem} />

                <Text size='lg' className='text-gray-400 mt-1'>
                    Faça Login e comece a usar!
                </Text>
            </header>

            <form onSubmit={handleSignIn} className='flex flex-col gap-4 items-stretch w-full max-w-sm mt-10'>
                {isUserSignedIn &&
                    <div className='flex flex-items justify-center gap-2 text-cyan-700'>
                        <UserCircle size={24} />
                        <Text size='sm' className=' text-cyan-700 font-bold'>Login Realizado</Text>
                    </div>
                }

                <label htmlFor="email" className='flex flex-col gap-3'>
                    <Text className="font-semibold">Endereço de e-mail</Text>
                    <TextInput.Root>
                        <TextInput.Icon>
                            <Envelope />
                        </TextInput.Icon>
                        <TextInput.Input id="email" type="email" placeholder='Digite seu e-mail' />
                    </TextInput.Root>

                </label>

                <label htmlFor="password" className='flex flex-col gap-3'>
                    <Text className="font-semibold">Sua senha</Text>
                    <TextInput.Root>
                        <TextInput.Icon>
                            <Lock />
                        </TextInput.Icon>
                        <TextInput.Input id="password" type="password" placeholder='********' />
                    </TextInput.Root>

                </label>
                <label htmlFor="remember" className='flex items-center gap-2'>
                    <Checkbox id="remember" />
                    <Text className="text-gray-400">Lembrar de mim por 30 dias</Text>

                </label>

                <Button type="submit" className='mt-4'>Entrar na plataforma</Button>


            </form>

            <footer className='flex flex-col items-center gap-4 mt-8'>
                <Text asChild size='sm'>
                    <a href="" className='text-gray-400 underline hover:text-gray-200'>Esqueceu sua senha?</a>
                </Text>
                <Text asChild size='sm'>
                    <a href="" className='text-gray-400 underline hover:text-gray-200'>Não possui uma conta? Crie uma agora!</a>
                </Text>

            </footer>

        </div >
    )
}

